import dayjs from "dayjs";
const relativeTime = require("dayjs/plugin/relativeTime");
require("dayjs/locale/zh-cn");
dayjs.locale("zh-cn");
dayjs.extend(relativeTime);
// Êó•ÊúüË∑ùÁ¶ª‰ªäÊó•
function getRelativeTime(dt) {
    return dayjs().from(dayjs(dt));
}

/**
 * ÁÆÄÂçïÁöÑÊó•ÊúüÊ†ºÂºèÂåñ
 *
 * @param {*} dt DateÂÆû‰æã
 * @param {object} opt polished:ÊòØÂê¶Ë°•ÈΩê‰∏§‰Ωç,separator:ËøûÊé•ÂàÜÈöîÁ¨¶
 */
function dateFormat(dt, separator = "-", polished = true) {
    let year = dt.getFullYear();
    let month = dt.getMonth() + 1;
    let date = dt.getDate();
    let str = polished
        ? `${year}${separator}${polish(month)}${separator}${polish(date)}`
        : `${year}${separator}${month}${separator}${date}`;
    return str;
}

function polish(val) {
    return val < 10 ? "0" + val : val;
}

function getTimeAgo(dateString) {
    // ‰ΩøÁî®dayjsËß£Êûê‰º†ÂÖ•ÁöÑÊó•ÊúüÂ≠óÁ¨¶‰∏≤
    const date = dayjs(dateString);
    const now = dayjs();

    // ËÆ°ÁÆóÂΩìÂâçÊó∂Èó¥‰∏éÁªôÂÆöÊó•Êúü‰πãÈó¥ÁöÑÂ∑ÆÂºÇ
    const diff = now.diff(date, "day");
    if (diff === 0) {
        if (now.diff(date, "minute") <= 60) {
            return { text: "ÂàöÂàö", icon: "el-icon-loading" };
        } else if (now.diff(date, "minute") <= 120) {
            return { text: "2Â∞èÊó∂ÂÜÖ", icon: "el-icon-time" };
        } else {
            return { text: date.format("HH:mm"), icon: "el-icon-time" };
        }
    } else if (diff === 1) {
        return { text: "Êò®Â§©", icon: "el-icon-time" };
    } else if (diff >= 2 && diff <= 365) {
        return { text: date.format("MM-DD"), icon: "el-icon-date" };
    } else {
        // Â¶ÇÊûúÊó∂Èó¥Â∑ÆË∂ÖËøá1Âπ¥
        return { text: date.format("YYYY-MM-DD HH:mm:ss"), icon: "el-icon-date" };
    }
}

export { dateFormat, getRelativeTime, getTimeAgo };
